import * as React from "react";
import Info from "../components/Info";
import formulaImgTasa from '../img/tasaInteres.PNG'
import formulaImgSimple from '../img/simple.PNG'
import formulaImgComp from '../img/compuesto.PNG'


const InterestRate = () => {
    const PARAGRAPH_TASA = ["Es el interes generado en una unidad de tiempo expresado en porcentaje. Las unidades de tiempo por lo general son expresadas por el perido de un ao (anual), auque tambien se lo pude expresar mensualmente, trimestral, diariamente, etc. "]
    const PARAGRAPH_SIMPLE = ["Es el que se optiene cuando los inters producidos duarante el tiempo que dura un prestamo, se deben unicamenta al capital inicial. Cuando se utilza el interez simple, los interese son nicamente en funcin del capital inicial, el nmero de periodos y la tasa de inters."]
    const PARAGRAPH_COMP = ["Es el inters generado tanto por el monto inicial como por el inters de los periodos anteriores, es decir aparte del inters del monto inicial, los intereses tambin generan intereses. Esta es una prctica adoptada por el sistema finaciero de bancos y cooperaticas."]

    let formulaDescriptionTasa = [
        "i(%) = Tasa de insters expresado en porcentaje",
        "I = Inters",
        "P = Monto Inicial",
        "n = Nmero de periodos de tiempo"
    ]
    let formulaDescriptionSimple = [
        "I = Inters",
        "P = Monto Inicial",
        "i = Tasa de insters",
        "n = Nmero de periodos de tiempo"
    ]
    return (
        <div className="App">

            <h2>Tasa de Inters</h2>
            <Info
                title="Tasa de inters"
                paragraph={PARAGRAPH_TASA}
                formulaImg={formulaImgTasa}
                descriptionFormula={formulaDescriptionTasa}
            />
            <h2>Tipos Inters</h2>
            <Info
                title="Inters simple"
                paragraph={PARAGRAPH_SIMPLE}
                formulaImg={formulaImgSimple}
                descriptionFormula={formulaDescriptionSimple}
            />
            <br/>
            <Info
                title="Inters compuesto"
                paragraph={PARAGRAPH_COMP}
                formulaImg={formulaImgComp}
                descriptionFormula={formulaDescriptionSimple}
            />
            <br/>
            <br/>


        </div>
    )
}

export default InterestRate;