import * as React from "react"
import Info from "../components/Info";
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'
import Card from '@mui/material/Card';
//import CardActions from '@mui/material/CardActions';
//import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';

import imgAPrice from '../img/credit/APrice.PNG';
import imgInteresPrice from '../img/credit/interesPrice.PNG';
import imgKPrice from '../img/credit/kPrice.PNG';
import imgKConst from '../img/credit/kConst.PNG';
import imgAConst from '../img/credit/AConst.PNG';
import imgKGracia from '../img/credit/kGracia.PNG';
import imgTablaPrice from '../img/credit/tablaPrice.PNG';
import imgTablaConst from '../img/credit/tablaConst.PNG';
import imgTablaGracia from '../img/credit/tablaGracia.PNG';
import TablePaymentPlan from "../components/TablePaymentPlan";
import FormPaymentPlan from "../components/FormPaymentPlan";

const DESCRIPTION_PRICE = [
  "El sistema francs de amortizacin consiste en la amortizacin de ste mediante una renta constante de n trminos. Es un sistema matemtico que se utiliza para amortizar un crdito. Su caracterstica principal radica en la cuota de amortizacin, ya que es igual para todo el perodo del prstamo, en crditos a tasa fija. Su clculo es complejo pero en lneas generales se puede decir que el capital se amortiza en forma creciente, mientras que los intereses se calculan sobre el saldo, motivo por el cual son decrecientes. Es el sistema de amortizacin ms difundido entre los bancos y usualmente va asociado a una tasa ms baja que el crdito con sistema alemn de amortizacin. Sin embargo, presenta la desventaja de que si existen posibilidades de precancelar el crdito en un lapso breve de su otorgamiento, el capital adeudado sea ms abultado.",
  "Cada anualidad es la suma de la cuota de inters y la cuota de amortizacin correspondiente al ao de que se trate. Este sistema se llama tambin progresivo, porque a medida que transcurre el tiempo las cuotas destinadas a la amortizacin de capital van siendo mayores, mientras que las cuotas de inters irn disminuyendo porque el capital pendiente por amortizar ir siendo menor."
]

const DESCRIPTION_CONST = [
  "En este tipo de prstamos la amortizacin de capital es constante en todas las cuotas. Tambin y a efectos de simplificar, vamos a considerar que el tipo de inters es constante durante toda la operacin, aunque este requisito no es necesario. Calculamos fcilmente el importe de la amortizacin de capital constante. Basta con dividir el importe del prstamo por el nmero de perodos."
]

const FORMULA_CONST = [
  {
    description: "La amortizacin ser:",
    img: imgKConst
  },
  {
    description: "El inters es:",
    img: imgInteresPrice
  },
  {
    description: "La cuota (Pago) es:",
    img: imgAConst
  },
  {
    description: "Representado en una tabla ser:",
    img: imgTablaConst
  },
]
const DESCRIPTION_GRACIA = [
  "La facilidad que se brinda con este concepto es que no existe amortizacin a capital durante el periodo o los periodos de gracia y solo se cancela inters ya sea para la amortizacin francs o amortizacin constante."
]

const FORMULA_GRACIA = [
  {
    description: "La amortizacin constante ser:",
    img: imgKGracia
  },
  {
    description: "El inters es:",
    img: imgInteresPrice
  },
  {
    description: "La cuota (Pago) es:",
    img: imgAConst
  },
  {
    description: "Representado en una tabla ser:",
    img: imgTablaGracia
  },
]

const Simulation = () => {

  const [dataTable, setDataTable] = React.useState([])
  let formulaPrince = [
    {
      description: "Para el clculo primero se debe calcular el valor de la serie A:",
      img: imgAPrice
    },
    {
      description: "El inters es:",
      img: imgInteresPrice
    },
    {
      description: "Para de determinar el valor de la amortizacin k:",
      img: imgKPrice
    },
    {
      description: "Representado en una tabla ser:",
      img: imgTablaPrice
    },
  ]
  return (
    <div className="App">
      <h2>Amortizacin de Deudas</h2>
      <Box sx={{ textAlign: "start" }}>
        <Typography variant="body2">
          Amortizacin es el proceso de cancelar una deuda con sus intereses por medio de pagos peridicos. La amortizacin toma curso cuando un prestarario le paga a su prestamista un monto de dinero en un cierto lapso de tiempo, incluyendo las correspondientes tasas de inters. La deuda puede extinguirse de una sola vez, o bien hacerlo en forma gradual por medio de pagos parciales por una determida cantidad de tiempo, la que ha sido previamente establecida.
          <br />
          <br />
          <Button variant="contained" size="large" href="#simulation">
            Simular Credito
          </Button>
          <br />
        </Typography>

      </Box>
      <Info
        title="Sistema de amortizacin francs PRICE"
        paragraph={DESCRIPTION_PRICE}
        formulaImg={null}
        descriptionFormula={[]}
      />
      {
        formulaPrince.map((element) => (
          <Box sx={{ margin: "30px" }}>
            <Typography variant="body2" sx={{ margin: "10px" }}>
              {element.description}
              <br />
            </Typography>
            <Card variant="outlined" sx={element.img === imgTablaPrice ? { width: 320, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" } : { width: 200, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" }}>

              <CardMedia
                sx={{ width: "100%", display: "block", margin: "auto" }}
                component="img"
                height="100%"
                image={element.img}
                alt="tasa"
              />
            </Card>
          </Box>

        ))

      }
      <Info
        title="Sistema de amortizacin constante"
        paragraph={DESCRIPTION_CONST}
        formulaImg={null}
        descriptionFormula={[]}
      />
      {
        FORMULA_CONST.map((element) => (
          <Box sx={{ margin: "30px" }}>
            <Typography variant="body2" sx={{ margin: "10px" }}>
              {element.description}
              <br />
            </Typography>
            <Card variant="outlined" sx={element.img === imgTablaConst ? { width: 320, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" } : { width: 200, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" }}>

              <CardMedia
                sx={{ width: "100%", display: "block", margin: "auto" }}
                component="img"
                height="100%"
                image={element.img}
                alt="tasa"
              />
            </Card>
          </Box>

        ))

      }
      <Info
        title="Perodo de Gracia"
        paragraph={DESCRIPTION_GRACIA}
        formulaImg={null}
        descriptionFormula={[]}
      />
      {
        FORMULA_GRACIA.map((element) => (
          <Box sx={{ margin: "30px" }}>
            <Typography variant="body2" sx={{ margin: "10px" }}>
              {element.description}
              <br />
            </Typography>
            <Card variant="outlined" sx={element.img === imgTablaGracia ? { width:320, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" } : { width:200, display: "block", marginTop: "20px", marginBottom: "10px", margin: "auto" }}>

              <CardMedia
                sx={{ width: "100%", display: "block", margin: "auto" }}
                component="img"
                height="100%"
                image={element.img}
                alt="tasa"
              />
            </Card>
          </Box>

        ))

      }
      <Box id="simulation">
        <FormPaymentPlan setDataTable={setDataTable} />
      </Box>
      <Box sx={{ display: "block", margin: "auto" }}>
        <h3>Plan de Crdito</h3>
        <TablePaymentPlan data={dataTable} />
        <br />
      </Box>

    </div>
  )
}
export default Simulation;